













import { Vue, Component, Prop, Mixins } from "vue-property-decorator";

@Component({})
export default class HelpComponent extends Mixins() {
  helpTopics = ["forgot-password", "backup-missing"];

  get helpSections() {
    return this.helpTopics.map(t => ({
      title: this.$t(`help.${t}.title`),
      content: this.$t(`help.${t}.content`)
    }));
  }
}
